import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import {
    Button,
} from "antd";
import { changLoadingState } from '../../actions/global';
import HeaderComponent from '../../components/Header';
import Advertising from '../../components/Advertising';

const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    return (
        <div>
            <HeaderComponent isBack={false}/>
            <Advertising />
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



