import React from 'react';
import {
    Card,
    Tag,
} from 'antd';
import advertisingImg1 from '../../assets/image/advertising/您的小棉袄.jpeg';
import './index.less';


const { Meta } = Card;

function Index(props) {
    const hasTouch = ("ontouchend" in document);
    const styleObj = {
        width: hasTouch ? '' : '240px',
    };
    return (
        <div
            className={'advertisingWrapper'}
        >
            <Card
                title={(
                    <div>
                        诚招广告,竞价排名
                        <br />
                        <Tag color="cyan">日均访问量俩万</Tag>
                    </div>
                )}
                bordered={false}
            >
                <Card
                    hoverable
                    style={styleObj}
                    cover={
                        <img alt="您的小棉袄广告位" src={advertisingImg1} />
                    }
                >
                    <Meta title="微信公众号：您的小棉袄" description="您的小棉袄公众号为大家全身心服务，只要您的需求与疑问全部进行专业解答！期待您的关注！" />
                </Card>
            </Card>
        </div>
    );
}

export default Index;



