import React from 'react';
import {Avatar, PageHeader} from "antd";
import defaultAvatarImage from "../../assets/image/avatar.jpg";

function Index(props) {
    const {
        avatarImage = defaultAvatarImage,
        title = '吴小迪的博客',
        description = 'CSDN超级吴小迪的个人博客地址，也可以通过wudi98.blog.csdn.net访问我的csdn博客地址',
        isBack = true,
    } = props;
    const header = (
        <div>
            <Avatar src={avatarImage} />
            {title}
        </div>
    );
    return (
        <PageHeader
            style={{
                border: '1px solid rgb(235, 237, 240)',
            }}
            title={header}
            subTitle={description}
            onBack={isBack ? (() => props.history.back()) : null}
        />
    );
}

export default Index;



